import * as THREE from "three";
import * as THREEx from "./ar-threex-location-only"

const main = ()=> {

// Scene
const scene = new THREE.Scene();

/**
 * Base + Box
 */

const video = document.getElementById("videotexture");
video.setAttribute('playsinline', true);
const texture = new THREE.VideoTexture(video);
texture.colorSpace = THREE.SRGBColorSpace;

const cube = new THREE.BoxGeometry(1, 1, 1);
const cubeMat = new THREE.MeshStandardMaterial({ color: 0xFFFFFF, map: texture });
cubeMat.roughness = 0.4;
const cubeMesh = new THREE.Mesh(cube, cubeMat);
cubeMesh.castShadow = true;
scene.add(cubeMesh);

/**
 * Sizes
 */

const sizes = {
  width: window.innerWidth,
  height: window.innerHeight
};

/**
 * lights
 */

const ambient = new THREE.AmbientLight();
ambient.intensity = 0.1;
scene.add(ambient);

const light1 = new THREE.PointLight({ color: "white" }, 1);
light1.position.set(0, 3, 2);
light1.shadow.mapSize.width = 1024;
light1.shadow.mapSize.height = 1024;
light1.shadow.radius = 5;
scene.add(light1);

/**
 * Camera
 */
const camera = new THREE.PerspectiveCamera(80, sizes.width / sizes.height);
camera.position.z = 3;
scene.add(camera);

/**
 * Renderer
 */
const canvas = document.querySelector("#webgl");
const renderer = new THREE.WebGLRenderer({
  canvas: canvas
});
renderer.setSize(sizes.width, sizes.height);
renderer.setPixelRatio( window.devicePixelRatio );
renderer.shadowMap.enabled = true;

const cam = new THREEx.WebcamRenderer(renderer, "#video1");

/**
 * Animate
 */


const isMobile = ()=> {
  if (
    /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
      navigator.userAgent
    )
  ) {
    return true;
  }
  return false;
}

let orientationControls = null;

  // Orientation controls only work on mobile device
  if (isMobile()) {
    /*DeviceOrientationEvent.requestPermission()
      .then((response) => {
        if (response == "granted") {
          window.addEventListener("deviceorientation", (e) => {
            // do something with e
          });
        }
      })
      .catch(console.error);

    orientationControls = new THREEx.DeviceOrientationControls(camera);*/
  }

const resizeUpdate = () =>{
  const canvas = renderer.domElement;
  const width = canvas.clientWidth,
    height = canvas.clientHeight;
  if (width != canvas.width || height != canvas.height) {
    renderer.setSize(width, height, false);
  }
  camera.aspect = canvas.clientWidth / canvas.clientHeight;
  camera.updateProjectionMatrix();
}

const tick = () => {
  resizeUpdate();

  if (orientationControls) orientationControls.update();

  cubeMesh.rotateX(0.01);
  cubeMesh.rotateY(0.03);
  // Render
  cam.update();
  renderer.render(scene, camera);

  // Call tick again on the next frame
  window.requestAnimationFrame(tick);
};

tick();
}

const startButton = document.getElementById( 'startButton' );
startButton.addEventListener( 'click', function () {
  const overlay = document.getElementById( 'overlay' );
	overlay.remove();

  main();
} );
